//这边基本上引入需要使用的资源less，api，需要使用的模块等等。

class auditproject extends Interstellar.modelBase {
    constructor(app) {
        super(app)
        this.apiData={page:1,pageSize:10}
        this.condition =[
            [{ "type": "dropdown", "name": "auditProjecId", "showname": "审核项目名称", "datatype": "obj", "data": [], "key": "auditProjecId", "out": true,"input":true }],
            [{ "type": "dropdown", "name": "importProjectId", "showname": "所属项目名称", "datatype": "obj", "data": [], "key": "importProjectId", "out": true,"input":true }],
            [{ "type": "dropdown", "name": "status", "showname": "状态类型", "datatype": "obj", "data": [{val: '未启用', idx: '1'}, {val: '已启用', idx: '2'}], "key": "status", "out": true }]
        ]
        this.tableconfig={
            icon:{
                "id": { name: '<span>项目ID</span>', type: 'text',code:'checkid', w: '10%', ww: '10%',n:"40" },
                "auditProjectName": { name: '<span>审核项目名称</span>', type: 'text',code:'pid', w: '25%', ww: '25%', },
                "importProjectList": { name: '<span>包含标注项目名称</span>', type: 'text',code:'pname', w: '25%', ww: '25%' },
                "taskCount": { name: '<span>审核源任务数量</span>', type: 'text',code:'psex', w: '15%', ww: '15%' },
                "status": { name: '<span>状态</span>', type: 'text',code:'age', w: '10%', ww: '10%' },
                "createTime": { name: '<span>创建日期</span>', type: 'text',code:'action', w: '15%', ww: '15%' }
            },
            actionulwidth:200,
            type:'center',
            actionicon:{
                "operation": { name: '<span data-i18n="action" data-name="操作">操作</span>', type: 'action',code:'action', w: '100%', ww: '100%' }
            }
        }
        this.listicon={
            action:{//未启用
                config: { dis: 'inline', link: 'noLink',content: [{text:'查看详情',key:'view'},{text:'启用',key:'start'},{text:'编辑',key:'edit'},{text:'复制',key:'copy'}] }
            },
            action1:{//已启用
                config: { dis: 'inline', link: 'noLink',content: [{text:'查看详情',key:'view'},{text:'暂停启用',key:'stop'},{text:'复制',key:'copy'},{text:'编辑',key:'edit'},{text:'查看审核结果',key:'viewres'},{text:'导出所有审核最终结果',key:'export'}] }
            },
            action2:{//已启用没有结果
                config: { dis: 'inline', link: 'noLink',content: [{text:'查看详情',key:'view'},{text:'暂停启用',key:'stop'},{text:'复制',key:'copy'},{text:'编辑',key:'edit'}] }
            }
        }

        this.searchconfig=[{
            type:'single',
            name:'name',
            showname:'审核项目名称',
            datatype:'obj',
            data:[]
        },{
            type:'single',
            name:'method',

            showname:'所属项目名称',
            datatype:'obj',
            data:[]
        },{
            type:'single',
            name:'status',
            showname:'状态类型',
            datatype:'obj',
            data:[{
                val: '未启用',
                idx: 'M'
            }, {
                val: '已启用',
                idx: 'F'
            }]
        }]

    }
}
module.exports = auditproject;


